import { Component } from '@angular/core';

import { Platform } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html'
})
export class AppComponent {
  constructor(
    private platform: Platform,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar
  ) {
    this.initializeApp();
  }

  initializeApp() {
    this.platform.ready().then(() => {
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }
  onCustomersListClick() {
    window.location.href = `${window.location.origin}/customers/home`;
  }
  onPoliciesListClick() {
    window.location.href = `${window.location.origin}/policies`;
  }
  onCasualtiesListClick() {
    window.location.href = `${window.location.origin}/casualties`;
  }
  onCasualtyNewClick() {
    window.location.href = `${window.location.origin}/casualties/casualty/new`;
  }
  onSchedulerListClick() {
    window.location.href = `${window.location.origin}/scheduler`;
  }
  onWordListClick() {
    window.location.href = `${window.location.origin}/world`;
  }
  onSetupListClick() {
    window.location.href = `${window.location.origin}/setup`;
  }
  onSetupPoliciesListClick() {
    window.location.href = `${window.location.origin}/setuppolicies`;
  }
  onSetupCasualtiesListClick() {
    window.location.href = `${window.location.origin}/setupcasualties`;
  }
  onLogout() {
    window.location.href = `${window.location.origin}/login/logout`;
  }
}
