import { Component, OnInit, Input } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { PolicyItem } from 'src/app/models/policy-item';
import { PolicyService } from 'src/app/services/policy.service';
import { CustomerService } from 'src/app/services/customer.service';
import { OrganizationService } from 'src/app/services/organization.service';
import { Customer } from 'src/app/models/customer';
import { Organization } from 'src/app/models/organization';
import { Policy } from 'src/app/models/policy';

@Component({
  selector: 'app-policy-modal',
  templateUrl: './policy-modal.page.html',
  styleUrls: ['./policy-modal.page.scss'],
})
export class PolicyModalPage implements OnInit {

  constructor(
    private mc: ModalController,
    private ps: PolicyService,
    private cs: CustomerService,
    private os: OrganizationService,
    ) { }
  @Input() customer: Customer;
  customerToken: string;
  basePolicies: PolicyItem[] = [];
  @Input() policies: PolicyItem[] = [];

  ngOnInit() {
  }

  getCustomer(token: string): Promise<Customer> {
    const self = this;
    return new Promise((resolve, reject) => {
      self.cs.asyncGetCustomer(token).subscribe({
        next(c) {
          resolve(c);
        },
        error(err) {
          reject(err);
        }
      });
    });
  }

  getCompanies(): Promise<Organization[]> {
    const self =  this;
    return new Promise((resolve, reject) => {
      self.os.getOrganizationList('Compagnie').subscribe({
        next(cs) {
          resolve(cs);
        },
        error(err) {
          reject(err);
        }
      });
    });
  }

  getPolicies(token: string): Promise<Policy[]> {
    const self = this;
    return new Promise((resolve, reject) => {
      self.ps.getListByCustomer(token).subscribe({
        next(ps) {
          resolve(ps);
        },
        error(err) {
          reject(err);
        }
      });
    });
  }

  async ionViewWillEnter() {
    try {
      this.customer = await this.getCustomer(this.customerToken);
      const companies = await this.getCompanies();
      const policies = await this.getPolicies(this.customerToken);
      this.basePolicies = policies.map(p => {
        const company: Organization = companies.reduce((a, b) => {
          if (b._id === p.insuranceCompanyId) {
            return b;
          } else {
            return a;
          }
        }, null);
        return {
          policy: p,
          customer: this.customer,
          insuranceCompany: company,
        };
      });
      this.policies = this.basePolicies;
    } catch (err) {
      console.log(err);
    }
  }

  DismissNoChoice() {
    this.mc.dismiss();
  }

  private filterData(query: string): Promise<PolicyItem[]> {
    const self = this;
    return new Promise ((resolve, reject) => {
      if (query && query !== '') {
        try {
          const regex = new RegExp(`${query}`, 'i');
          resolve(self.basePolicies.filter(obj => {
            const srep = JSON.stringify(obj);
            return regex.test(srep);
          }));
        } catch (e) {
          resolve(self.basePolicies);
        }
      } else {
        resolve(this.basePolicies);
      }
    });
  }

  async searchChange(event) {
    this.policies = await this.filterData(event.target.value);
  }

  onItemClick(p: PolicyItem) {
    this.mc.dismiss(p);
  }

}
