import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { RouteReuseStrategy } from '@angular/router';

import { IonicModule, IonicRouteStrategy } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';

import { PolicyModalPageModule } from 'src/app/modals/policy-modal/policy-modal.module';
import { PolicyModalPage } from 'src/app/modals/policy-modal/policy-modal.page';

import { OrganizationModalPageModule } from 'src/app/modals/organization-modal/organization-modal.module';
import { OrganizationModalPage} from 'src/app/modals/organization-modal/organization-modal.page';

import { AppComponent } from './app.component';
import { AppRoutingModule } from './app-routing.module';
import { AuthInterceptor } from 'src/app/interceptors/auth-interceptor';

@NgModule({
  declarations: [AppComponent],
  imports: [
    BrowserModule,
    IonicModule.forRoot(),
    AppRoutingModule,
    HttpClientModule,
    PolicyModalPageModule,
    OrganizationModalPageModule,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    { provide: RouteReuseStrategy, useClass: IonicRouteStrategy },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: AuthInterceptor,
      multi: true,
    },
  ],
  entryComponents: [
    PolicyModalPage,
    OrganizationModalPage,
  ],
  bootstrap: [AppComponent]
})
export class AppModule {}
