import { Component, OnInit, Input } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { Organization } from '../../models/organization';
import { OrganizationService } from 'src/app/services/organization.service';

@Component({
  selector: 'app-organization-modal',
  templateUrl: './organization-modal.page.html',
  styleUrls: ['./organization-modal.page.scss'],
})
export class OrganizationModalPage implements OnInit {

  baseOrganizations: Organization[] = [];
  @Input() organizations: Array<Organization> = [];
  kind = '';

  constructor(
    private mc: ModalController,
    private os: OrganizationService,
  ) {
  }

  ngOnInit() {
  }

  private getList(kind: string): Promise<Organization[]> {
    const self = this;
    return new Promise((resolve, reject) => {
      self.os.getOrganizationList(kind).subscribe({
        next(o) {
          resolve(o);
        },
        error(err) {
          reject(err);
        }
      });
    });
  }

  async ionViewWillEnter() {
    this.baseOrganizations = await this.getList(this.kind);
    this.organizations = this.baseOrganizations;
  }

  private filterData(query: string): Promise<Array<Organization>> {
    const self = this;
    return new Promise ((resolve, reject) => {
      if (query && query !== '') {
        try {
          const regex = new RegExp(`${query}`, 'i');
          resolve(self.baseOrganizations.filter(obj => {
            const srep = JSON.stringify(obj);
            return regex.test(srep);
          }));
        } catch (e) {
          resolve(self.baseOrganizations);
        }
      } else {
        resolve(this.baseOrganizations);
      }
    });
  }

  async searchChange(event) {
    this.organizations = await this.filterData(event.target.value);
  }

  onItemClick(o: Organization) {
    this.mc.dismiss(o);
  }

  DismissNoChoice() {
    this.mc.dismiss();
  }
}
