import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { Policy } from '../models/policy';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class PolicyService {
  baseUrl = '/api/policies';
  constructor(private hc: HttpClient) { }

  getList(kind?: string): Observable<Policy[]> {
    const mayKind = kind ? kind : 'policy';
    let session = window.localStorage.getItem('_session');
    if (! session) {
      session = 'loggedout';
    }
    return this.hc.get<Policy[]>(`${this.baseUrl}?kind=${mayKind}`, {
      headers : {
        's-t': session
      }
    });
  }

  getListByCustomer(token: string): Observable<Policy[]> {
    const mayKind = 'policy';
    let session = window.localStorage.getItem('_session');
    if (! session) {
      session = 'loggedout';
    }
    return this.hc.get<Policy[]>(`${this.baseUrl}?kind=${mayKind}&customerId=${token}`, {
      headers : {
        's-t': session
      }
    });
  }

  single(id: string): Observable<Policy> {
    let session = window.localStorage.getItem('_session');
    if (! session) {
      session = 'loggedou';
    }
    return this.hc.get<Policy>(`${this.baseUrl}/${id}`, {
      headers: {
        's-t': session
      }
    });
  }

}
