import { Injectable } from '@angular/core';
import { ServiceStatus } from '../models/service-status.enum';
import { Observable } from 'rxjs';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { Customer } from '../models/customer';

export class ServiceResponse {
  status: ServiceStatus;
  token: string;
}

@Injectable({
  providedIn: 'root'
})
export class CustomerService {

  constructor(private http: HttpClient) { }

  asyncGetCustomers(): Observable<Array<Customer>> {
    return new Observable((observer) => {
      let session = window.localStorage.getItem('_session');
      if (! session) {
        session = 'loggedout';
      }
      this.http.get(`http://pratiche.ares/api/customers`, {
        headers : {
          's-t': session
        },
        observe: 'response'
      }).subscribe((data: any) => {
        const primitiveCstms = data.body;
        const cstms: Array<Customer> = primitiveCstms.map((obj) => {
          const k = new Customer();
          k.firstName = obj.firstName;
          k.lastName = obj.lastName;
          k.fiscalCode = obj.fiscalCode;
          k.vatNumber = obj.vatNumber;
          k.birth = obj.birth;
          k.idCard = obj.idCard;
          k.passport = obj.passport;
          k.driveLicense = obj.driveLicense;
          k.address = obj.address;
          // k.options = obj.options;
          k.notes = obj.notes;
          k.contacts = obj.contacts;
          k.token = obj.token;
          k.version = obj.version;
          k.company = obj.company;
          k.deleted = obj.deleted;
          k._id = obj._id;
          return k;
        });
        observer.next(cstms);
        return {unsubscribe() {}};
      }, error => {
        console.log(error);
        observer.error(error);
        return {unsubscribe() {}};
      });
    });
  }
  asyncGetCustomer(token: string): Observable<Customer> {
    return new Observable((observer) => {
      let session = window.localStorage.getItem('_session');
      if (! session) {
        session = 'loggedout';
      }
      this.http.get(`http://pratiche.ares/api/customers/${token}`, {
        headers : {
          's-t': session
        },
        observe: 'response'
      }).subscribe((data: any) => {
        const obj = data.body;
        const k = new Customer();
        k.firstName = obj.firstName;
        k.lastName = obj.lastName;
        k.fiscalCode = obj.fiscalCode;
        k.vatNumber = obj.vatNumber;
        k.birth = obj.birth;
        k.idCard = obj.idCard;
        k.passport = obj.passport;
        k.driveLicense = obj.driveLicense;
        k.address = obj.address;
        k.options = new Map<string, string>();
        if (JSON.stringify(obj.options) !== JSON.stringify({})) {
          Object.keys(obj.options).forEach(key => {
            k.options.set(key, obj.options[key]);
          });
        }
        k.notes = obj.notes;
        k.contacts = obj.contacts;
        k.token = obj.token;
        k.version = obj.version;
        k.company = obj.company;
        k.deleted = obj.deleted;
        k._id = obj._id;
        console.log(k);
        observer.next(k);
        return {unsubscribe() {}};
      }, error => {
        console.log(error);
        observer.error(error);
        return {unsubscribe() {}};
      });
    });
  }
}
